.addTask{
    padding:20px 15px;
    border-radius: 5px;
    background-color: #FFFFFF;
    max-width:500px;
    margin:20px auto;
}

.addTask form{
    display:flex;
    gap:7px;
}

.addTask form input{
    flex-grow:1;
    padding:5px 5px;
    border: 1px solid #eaeaea;
    border-radius:5px;
    font-size:18px;
}

.addTask form input:focus{
    outline:0.5px solid #eaeaea;
}

.addTask form button{
    background-color: #03ba5c;
    color:#FFFFFF;
    border:none;
    padding:5px 20px;
    border-radius:5px;
    cursor:pointer;
    font-size:18px;
}

.addTask form button:hover{
    background-color: #03b056;
}